  <template>
    <div class="address-container">

      <div v-if="!addressList.length" class="add-address-img">
        <img :src="packUrl" alt="package image">
      </div>

      <button @click="toAddressMenu" class="add-address">
        添加新的收货地址
      </button>

    </div>
  </template>

  <script>
  import card from '@/components/card'

  export default {
    data () {
      return {
        motto: 'Hello World1',
        userInfo: {},
        packUrl: '../../images/package.png',
        addressList: {}
      }
    },

    components: {
      card
    },

    methods: {
      toAddressMenu () {
        const url = '../addressmenu/addressmenu'
        wx.navigateTo({ url })
      },
      getUserInfo () {
        // 调用登录接口
        wx.login({
          success: () => {
            wx.getUserInfo({
              success: (res) => {
                this.userInfo = res.userInfo
              }
            })
          }
        })
      },
      clickHandle (msg, ev) {
        console.log('clickHandle:', msg, ev)
      }
    },

    created () {
      // 调用应用实例的方法获取全局数据
      this.getUserInfo()
    }
  }
  </script>

  <style scoped>
  .address-container {
    height: 100%;
    width: 750rpx;
    background-color: #e6e6e6;
  }

  .add-address-img {
    height: 500rpx;
    width: 750rpx;
  }

  .add-address {
    height: 80rpx;
    width: 400rpx;
    color: white;
    margin-top: 50rpx;
    background-color: #63B8FF;
  }

  img {
    width: 100%;
    height: 100%;
  }

  </style>



